export default {
	data() {
		return {
			phone: "",
			ver_code: "",
			opt_type: ""
		}
	},
	
	methods: {
		
		getCode(li) {
			this.opt_type = li
			this.$H.post("/agent/", {
				mobile: this.phone,
				opt: li,
			}).then((res) => {
				
			}).catch((e) => {
				console.log("catch error!!", e);
			})
		},
		login(li) {
			this.$H.post("/agent/", {
				mobile: this.phone,
				opt: li,
				verify_code: this.ver_code
			}).then((res) => {
				if(res) {
					try {
					    uni.setStorageSync('user_id', res.user_id);
						uni.setStorageSync('token', res.token);
						uni.navigateTo({
							url: "/pages/index/index"
						})
					} catch (e) {
					    console.log('error catch:', e);
					}
				}
				console.log('res:', res);
			}).catch((e) => {
				console.log("catch error!!", e);
			})
			this.__init()
		},
		
	}
}
